(function() {
  angular.module('homemademessClient', ['ngCookies', 'ngSanitize', 'ngAria', 'ngResource', 'ui.router', 'akoenig.deckgrid', 'ngMaterial', 'ngDropzone', 'ngTagsInput', 'ngLodash']).filter('taglink', function() {
    return function(input) {
      return input.replace(/\s/g, '_');
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').directive('searchbar', function() {
    var SearchCtrl, directive;
    SearchCtrl = [
      '$scope', '$resource', 'lodash', '$q', '$location', 'apiUrl', function($scope, $resource, lodash, $q, $location, apiUrl) {
        var Auto, Images;
        $scope.tags = [];
        $scope.isActive = function(route) {
          return route === $location.path();
        };
        $scope.mapTags = function(tags) {
          return lodash.uniq(lodash.map(tags, '_id'));
        };
        Images = $resource(apiUrl + '/images');
        $scope.redoSearch = function() {
          return Images.get({
            tags: $scope.mapTags($scope.tags)
          }, function(result) {
            return $scope.view.images = result.images;
          });
        };
        Auto = $resource(apiUrl + '/auto');
        return $scope.findTags = function(value) {
          return Auto.query({
            q: value
          }).$promise;
        };
      }
    ];
    return directive = {
      restrict: 'E',
      templateUrl: 'app/components/searchbar/searchbar.html',
      controller: SearchCtrl
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').directive('openSlide', [
    '$document', '$rootScope', function($document, $rootScope) {
      var directive;
      return directive = {
        restrict: 'A',
        link: function(scope, element, attr) {
          return element.on('click', function(event) {
            event.preventDefault();
            return scope.showSlide(Number(attr.openSlide));
          });
        }
      };
    }
  ]);

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').directive('keyboard', [
    '$document', '$rootScope', function($document, $rootScope) {
      var directive;
      return directive = {
        restrict: 'A',
        link: function(scope, element, attr) {
          return $document.bind('keyup', function(e) {
            return $rootScope.$broadcast('keyup:' + e.which, e);
          });
        }
      };
    }
  ]).directive('keyboardNext', [
    function() {
      var directive;
      return directive = {
        restrict: 'A',
        scope: false,
        link: function(scope, element, attr) {
          return scope.$on('keyup:39', function(n, e) {
            if (scope.i < scope.slides.length - 1) {
              scope.i = scope.i + 1;
              return scope.$digest();
            }
          });
        }
      };
    }
  ]).directive('keyboardPrev', [
    function() {
      var directive;
      return directive = {
        restrict: 'A',
        scope: false,
        link: function(scope, element, attr) {
          return scope.$on('keyup:37', function(n, e) {
            if (scope.i > 0) {
              scope.i = scope.i - 1;
              return scope.$digest();
            }
          });
        }
      };
    }
  ]).directive('keyboardStart', [
    function() {
      var directive;
      return directive = {
        restrict: 'A',
        scope: false,
        link: function(scope, element, attr) {
          return scope.$on('keyup:38', function(n, e) {
            if (scope.i !== 0) {
              scope.i = 0;
              return scope.$digest();
            }
          });
        }
      };
    }
  ]).directive('keyboardEnd', [
    function() {
      var directive;
      return directive = {
        restrict: 'A',
        scope: false,
        link: function(scope, element, attr) {
          return scope.$on('keyup:40', function(n, e) {
            if (scope.i !== scope.slides.length - 1) {
              scope.i = scope.slides.length - 1;
              return scope.$digest();
            }
          });
        }
      };
    }
  ]);

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').controller('MainCtrl', function($scope, $http, $stateParams, apiUrl, $mdDialog, $document) {
    var getURI;
    $scope.view = {};
    $scope.i = 0;

    /*$scope.$watch 'i', (v, old) ->
      console.log 'i change', old, v
      $scope.slide = $scope.view.images[v]
     */
    if ($stateParams.tag) {
      getURI = apiUrl + '/tagged/' + $stateParams.tag;
      $scope.containerClass = 'tagged';
      $scope.tag = $stateParams.tag.replace(/_/g, ' ');
    } else {
      getURI = apiUrl + '/images';
      $scope.containerClass = 'main';
    }
    $http.get(getURI).success(function(result) {
      $scope.view.images = result.images;
      $scope.view.tags = result.tags;
      return $scope.view.offset = 0;
    });
    $scope.aspect = function(img, n) {
      var a, aspect, columns, rows;
      a = Math.round((img.width / img.height) * 100) / 100;
      if (a < 1) {
        columns = (function() {
          switch (false) {
            case !(a < 0.5):
              return 8;
            case !(a < 0.25):
              return 6;
            case !(a < 0.25):
              return 4;
            default:
              return 12;
          }
        })();
        rows = Math.round(columns * img.height / img.width);
      } else {
        rows = (function() {
          switch (false) {
            case !(a > 1.51):
              return 12;
            case !(a > 2):
              return 16;
            case !(a > 3):
              return 24;
            default:
              return 8;
          }
        })();
        columns = Math.round(rows * a);
      }
      aspect = [columns, rows];
      return aspect[n];
    };
    return $scope.showSlide = function(slide) {
      var locals;
      if (window.innerWidth > 459) {
        locals = {
          i: Number(slide),
          slides: $scope.view.images
        };
        return $mdDialog.show({
          clickOutsideToClose: true,
          locals: locals,
          template: '<md-dialog aria-label="{{slide.filename}}" class="slide">' + '  <md-dialog-content keyboard-next keyboard-prev keyboard-start keyboard-end>' + '     <img ng-src="{{img}}" class="img-responsive" ng-style="{{stylez}}" />' + '  </md-dialog-content>' + '</md-dialog>',
          controller: function($scope, $mdDialog, i, slides) {
            $scope.slides = slides;
            $scope.i = i;
            $scope.img;
            return $scope.$watch('i', function(v, old) {
              return $scope.img = $scope.slides[v].derivative[2].uri;
            });
          }
        });
      }
    };
  });

}).call(this);


(function() {
  angular.module('homemademessClient').run(function($log) {
    'ngInject';
    return $log.debug('runBlock end');
  });

}).call(this);


(function() {
  angular.module('homemademessClient').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $stateProvider.state('home', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainCtrl',
      controllerAs: 'main'
    }).state('tagged', {
      url: '/tagged/:tag',
      templateUrl: 'app/main/main.html',
      controller: 'MainCtrl'
    });
    return $urlRouterProvider.otherwise('/');
  });

}).call(this);


(function() {
  angular.module('homemademessClient').constant('apiUrl', 'http://api.homemademess.com');

}).call(this);


(function() {
  angular.module('homemademessClient').config(function($logProvider) {
    'ngInject';
    return $logProvider.debugEnabled(true);
  });

}).call(this);


